<template>
  <div class="reservation-wrap" @click.stop>
    <div class="content">
      <div class="title">预约信息确认 <span class="close-icon" @click.stop="fnChangeReservationShowFlag"></span></div>
      <div class="content-input-field">
        <div class="tel input-field">
          <van-field v-model="mobile" placeholder="请输入手机号" />
        </div>
        <div class="code input-field">
          <van-field v-model="mobileCode" placeholder="请输入验证码" />
          <div class="btn" :class="{loading: sendLoading}" @click.stop="sendSmsCaptcha">{{ sendBtnText }}</div>
        </div>
      </div>
      <div class="tips">
        <p>温馨提示：</p>
        <p>1.我们会向此手机号发送抢购信息通知，请认真 填写；</p>
        <p>2.提交后信息不可修改；</p>
        <p>3.预约成功不代表购买成功;</p>
      </div>
      <div class="content-btn">
        <div class="btn cancle" @click.stop="fnChangeReservationShowFlag">取消</div>
        <div class="btn confirm" @click.stop="reserve">确定</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mobile: '',
      mobileCode: '',
      sendLoading: false,
      sendBtnText: '获取验证码',
    }
  },
  methods: {
    sendSmsCaptcha() {
      if (this.sendLoading) {
        return
      }
      if (this.mobile.trim() === '') {
        this.$toast('请输入手机号')
        return
      }
      if (!(/^1[3456789]\d{9}$/.test(this.mobile))) {
        this.$toast('手机号格式不正确')
        return
      }
      this.$emit('sendSmsCaptcha', this.mobile)
      this.sendLoading = true
      this.sendBtnText = '重新发送(59s)'
      let time = 58
      let timeId = setInterval(() => {
        this.sendBtnText = `重新发送(${time}s)`
        if (time === 0) {
          this.sendLoading = false
          this.sendBtnText = '获取验证码'
          clearInterval(timeId)
        }
        time--
      }, 1000)
    },
    reserve() {
      if (this.mobile.trim() === '') {
        this.$toast('请输入手机号')
        return
      }
      if (!(/^1[3456789]\d{9}$/.test(this.mobile))) {
        this.$toast('手机号格式不正确')
        return
      }
      if (this.mobileCode.trim() === '') {
        this.$toast('请输入验证码')
        return
      }
      this.$emit('reserve', this.mobile, this.mobileCode)
      this.fnChangeReservationShowFlag()
      this.mobile = ''
      this.mobileCode = ''
    },
    fnChangeReservationShowFlag() {
      this.$emit('fnChangeReservationShowFlag')
    },
  },
}
</script>
<style lang="scss" scoped>
.reservation-wrap {
  background-color: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  padding-left: 1.06rem;
  padding-right: 1.06rem;
  z-index: 70;
}
</style>
